.brand-border {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 240px;
    height: 240px;
    -webkit-transform: translate(0%, -15%);
            transform: translate(0%, -15%);
    box-sizing: border-box;
    overflow: hidden;
}

.brand-border:before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    transition: 0.5s;
    pointer-events: none;
}

.brand-border span{
   position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    box-sizing: border-box;
}
.brand-border span:nth-child(1){
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.brand-border span:nth-child(2){
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
.brand-border span:nth-child(3){
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.brand-border span:nth-child(4){
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
}
.brand-border span:nth-child(2):before{
    -webkit-animation-delay: -2s;
            animation-delay: -2s;
}
 .brand-border span:nth-child(4):before{
    -webkit-animation-delay: -2s;
            animation-delay: -2s;
}
.brand-border span:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    background: #0093ff;
    -webkit-animation: animate 4s linear infinite;
            animation: animate 4s linear infinite;
}
@-webkit-keyframes animate{
    0%{
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50.1%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        -webkit-transform-origin: right;
                transform-origin: right;
    }
    100%{
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transform-origin: right;
                transform-origin: right;
    }
}
@keyframes animate{
    0%{
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        -webkit-transform-origin: left;
                transform-origin: left;
    }
    50.1%{
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        -webkit-transform-origin: right;
                transform-origin: right;
    }
    100%{
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transform-origin: right;
                transform-origin: right;
    }
}

