}

.inputBox span {
    font-size: .75rem;
    position: absolute;
    left: 0;
    padding: 10px 0 5px;
    color: rgba(255, 255, 255, 0.5);
    pointer-events: none;
    letter-spacing: 0.1em;
    transition: 0.5s;
}

.inputBox input:valid ~ span, 
.inputBox input:focus ~ span {
    color: rgba(255, 255, 255, 0.5);
    font-size: .75rem;
    opacity: 0;
}

.inputBox i {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    overflow: hidden;
}

.inputBox i::before {
    content: '';
    position: absolute;
    left: -100%;
    width: 100%;
    height: 100%;
    background: rgb(139, 51, 132);
    -webkit-animation: animates 2s linear infinite;
            animation: animates 2s linear infinite;
    transition: 0.5s;
}

.inputBox input:valid ~ i::before, 
.inputBox input:focus ~ i::before {
    left: 0;
}

@-webkit-keyframes animates {
