.stepone-unactive::before,
.steptwo-unactive::before {
    display: none;
}
.woocommerce-checkout-form {
    align-items: flex-start;
    flex-wrap: wrap;
    max-width: 768px;
    margin: 0 auto;
}
.checkout-order-wrap .heading {
    width: 100%;
    color: #c6d4df;
    font-size: 1.5em;
    font-weight: 600;
    float: none;
    padding: 0;
    margin-top: 1em;
    margin-bottom: 0.7em;
    border-bottom: 0;
    border: 0;
}
.order-review {
    width: 100%;
    float: none;
    margin-bottom: 0;
    padding: 0;
    border: 0;
}
.order-table {
    width: 100%;
    text-align: left;
    margin: 0 -1px 24px 0;
    box-shadow: #c6d4df;;
    border-radius: 4px;
    border: 1px solid #dddddd;
    border-bottom: 1px solid #c6d4df;
}
.product-name,
.subtotal {
    color: #c6d4df;;
    font-weight: 500;
    line-height: 1.4;
    padding: 1em 1.2em;
}
.entry-content .cart-empty-div {
    width: 100%;
}
.entry-content .cart-empty {
    font-size: 1.4rem;
    text-align: right;
}
.subtotal {
    text-align: right;
}
.product-amount {
    text-align: center;
}
.order-cart-item .product-name-td{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: #c6d4df;;
    line-height: 1.4em;
    font-weight: 400;
    padding-right: 0;
    padding: 1.5em 1.2em;
    border-color: #c6d4df;;
    border-top: 0;
    border-right: 0;
}
.order-cart-item .product-image {
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    vertical-align: middle;
    border-radius: 5px;
    padding-bottom: 0.3em;
}
.product-thumbnail img {
    max-width: 100%;
    width: 50px;
    height: 50px;
    border-radius: 4px;
    margin-right: 10px;
    vertical-align: middle;
}
.order-cart-item .product-image .product-name {
    color: #c6d4df;;
    width: calc(100% - 60px);
}
.order-cart-item .product-quantity {
    width: auto;
    font-weight: 400;
}
.td-product-total {
    border-color: #c6d4df;;
    text-align: right;
    font-weight: 400;
    line-height: 1.4em;
    padding: 1.5em 1.2em;
    border-right: 0;
}
.cart-subtotal th,
.order-total th{
    color: #c6d4df;;
    padding: 1.5em 1.2em;
    line-height: 1.4em;
    font-weight: 400;
    border-right: 0;
}
.cart-subtotal td,
.order-total td{
    color: #c6d4df;;
    text-align: right;
    line-height: 1.4em;
    font-weight: 400;
    padding: 1.5em 1.2em;
    border-right: 0;
}
.payment-option-heading h3{
    color: #c6d4df;;
    font-family: 'Poppins',sans-serif;
    font-size: 1.5em;
    font-weight: 600;
    line-height: 1.2em;
    padding: 0;
    margin-top: 1em;
    margin-bottom: 0.7em;
    border: 0;
}
.payment-method {
    padding: 0;
    margin: 0 0 0.5em;
    border: 1px solid #c6d4df;;
    border-radius: 4px;
}
.payment-method li {    
    display: inline-table;
    width: 100%;
    min-height: 2.7em;
    padding: 15px 15px 0;
}
.payment-method .paypal {    
    position: relative;
    font-size: 2rem;
    vertical-align: middle;
    margin: -2px 0 0 0.5em;
    padding: 0;
    box-shadow: none;
}
.payment-method-paypal {
    font-weight: 500;
    display: inline-block;
    width: calc(100% - 10%);
    color: #c6d4df;
    font-size: 1em;
}
.payment-suggest {
    width: calc(100% + 30px);
    color: #c6d4df;
    background-color: #1B2838;
    border-top: 1px solid #dddddd;
    border-radius: 0 0 4px 4px;
    padding: 1.5em;
    margin: 15px 0 0 -15px;
}
.border-top  {
    border-top: 1px solid #c6d4df;
}
.showCoupon{
    display: none;
}
.hideCoupon {
    display: block;
}
.code-msg {
    position: relative;
    width: auto;
    color: #c6d4df;
    background-color: #45556c;
    zoom: 1;
    word-wrap: break-word;
    list-style: none outside;
    border-top-color: #a46497;
    border-top: 3px solid #b81c23;
    padding: 1em 2em 1em 3.5em;
    margin: 0 0 2em;
}
.code-msg span {
    color: #f9aa01;
}
.error-icon {
    color: red;
    padding-right: 10px;
}
.check-icon {
    color: green;
    padding-right: 10px;
}
.exist {
    color: green;
}
.instructions {
    border: 1px solid red;
}
.offscreen {
    border: 1px solid #2A475E;
}
.mb {
    display: none;
}
.pc {
    display: block;
}
@media screen and (max-width: 992px) {
    .checkout-box {
        padding: 0px;
        max-width: 90%;
        margin: 0 auto;
        padding-block: 5.34em;
    }
}
@media screen and (max-width: 804px) {
    .order-cart-item .product-name-td {
        padding: 0.5em 0.3em;
    }
    .product-name ,.subtotal {
        padding: 1em 0.3em;
    }
    .td-product-total,
    .cart-subtotal td, .order-total td,
    .cart-subtotal th, .order-total th,
    .cart-subtotal td, .order-total td {
        padding: 1.5rem .3em;
    }
    .pc {
        display: none;
    }
    .mb {
        display: block;
    }
}

@media screen and (max-width: 629px) {
    .order-review {
        display: flex;
        flex-wrap: wrap;
    }
    .custom-form-input {
        font-size: .70rem;
    }
    .town, .postcode{
        width: 50%;
    }
    .st-name, .apartment {
        width: 100%;
    }
    .custom-form-input_country {
        height: 45px;
    }
    .order-cart-item .product-image {
        display: block;
    }
    .title {
        display: none;
    }
    .product-name, .subtotal {
        font-size: .80rem;
        padding: 1em 0em;
    }
    .td-product-total {
        font-size: .90rem;
    }
    .cart-subtotal th, .order-total th {
        font-size: .90rem;
    }
    .pc {
        display: none;
    }
    .mb {
        display: block;
    }
    .next-btn {
        padding: 0rem;
    }
    .button-text,
    .btn-sub-text {
        font-size: .80rem;
    }
    .code-msg  {
        font-size: .70rem;
        padding: 1em;
    }
}
@media screen and (max-width: 449px) {
    .step-heading {
        font-size: .80rem;
    }
    .product-name, .subtotal {
        font-size: .70rem;
        padding: 1em 0em;
    }
    .td-product-total {
        font-size: .70rem;
    }
    .cart-subtotal th, .order-total th {
        font-size: .70rem;
    }
    .pc {
        display: none;
    }
    .mb {
        display: block;
        padding-left: 3px;
    }
}
@media screen and (max-width: 349px) {
    .step-heading{
        font-size: .60rem;
    }
    .checkout-container .submit-detail {
        padding: .70rem 0rem;
    }
    .button-text,
    .btn-sub-text {
        font-size: .70rem;
    }
    .show-coupon {
        display: flex;
        flex-direction: column;
    }
    .show-coupon input {
        width: 100%;
    }
    .show-coupon .apply-coupon {
        width: 100%;
        margin-top: 1rem;
    }    
    .code-msg  {
        font-size: .50rem;
        padding: 1em 0.5em; 
    }
}
